import { FormsModule } from '@angular/forms';
import { SendoK80Component } from './sendo-printer/sendo-k80/sendo-k80.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SendoComponent } from './sendo.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { SendoPrinterComponent } from './sendo-printer/sendo-printer.component';
import { A4Component } from './sendo-printer/a4/a4.component';
import { A5Component } from './sendo-printer/a5/a5.component';
import { A6Component } from './sendo-printer/a6/a6.component';
import { B6Component } from './sendo-printer/b6/b6.component';
import { B7Component } from './sendo-printer/b7/b7.component';

const routes: Routes = [
  {
    path: '',
    component: SendoComponent
  },
  {
    path: 'print',
    component: SendoPrinterComponent
  }, 
  {
    path: 'affiliate-product',
    loadChildren: () => import('./affiliate-product/affiliate-product.module').then(m => m.AffiliateProductModule)
  },  
  {
    path: 'order-status',
    loadChildren: () => import('./order-status/order-status.module').then(m => m.OrderStatusModule)
  },
  {
    path: 'update-selling-price',
    loadChildren: () => import('./update-selling-price/update-selling-price.module').then(m => m.UpdateSellingPriceModule)
  },
  {
    path: 'update-stock',
    loadChildren: () => import('./update-stock/update-stock.module').then(m => m.UpdateStockModule)
  },
  {
    path: 'return-refund',
    loadChildren: () => import('./return-refund/return-refund.module').then(m => m.ReturnRefundModule)
  },
  // {
  //   path: 'export-goods',
  //   loadChildren: () => import('./control-export-goods/control-export-goods.module').then(m => m.ControlExportGoodsModule)
  // },
  {
    path: 'issue-invoice',
    loadChildren: () => import('./issue-invoice/issue-invoice.module').then(m => m.IssueInvoiceModule)
  },
  // {
  //   path: 'control-exel',
  //   loadChildren: () => import('./control-exel/control-exel.module').then(m => m.ControlExelModule)
  // },
  // {
  //   path: 'return-control',
  //   loadChildren: () => import('./return-control/return-control.module').then(m => m.ReturnControlModule)
  // },
  {
    path: 'product-on-shop',
    loadChildren: () => import('./product-on-shop/product-on-shop.module').then(m => m.ProductOnShopModule)
  }, {
    path: 'report',
    loadChildren: () => import('./report/report.module').then(m => m.ReportModule)
  }, {
    path: 'auto-sale',
    loadChildren: () => import('./auto-sale/auto-sale.module').then(m => m.AutoSaleModule)
  }
  , {
    path: 'control',
    loadChildren: () => import('./control/control.module').then(m => m.ControlModule)
  }
]

@NgModule({
  declarations: [
    SendoComponent,
    SendoPrinterComponent,
    SendoK80Component,
    A4Component,
    A5Component,
    A6Component,
    B6Component,
    B7Component
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class SendoModule { }
