import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sendo-printer',
  templateUrl: './sendo-printer.component.html',
  styleUrls: ['./sendo-printer.component.scss']
})
export class SendoPrinterComponent implements OnInit {
  root: any;
  constructor(
    private router: Router
  ) {
    this.root = this.router.getCurrentNavigation().extras.state;
    let total = 0
    this.root.bill.sku_details.forEach(item => total += item.quantity)
    this.root.bill.total_product = total
  }

  ngOnInit(): void {
    

  }
  goBack() {
    this.router.navigate([this.root.path], { state: this.root })
  }

}
